import Upload from '@/components/Student/Upload.vue'
import ComponentTabs from '@/components/Student/ComponentTabs.vue'
import cloneDeep from 'lodash.clonedeep'
import { participationDegree } from '@/helpers/select-data'
import { ACHIEVEMENT, INDICATOR } from '@/helpers/constants'

export default {
  components: { Upload, ComponentTabs },
  data() {
    return {
      loading: true,
      participationDegree: participationDegree,
      buttonDisabled: false,
      loading: false,
      indicators: [],
      activeNomination: null,
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.user.userInfo
    },
    initialIndicators() {
      return this.$store.state.app.indicator
    },
    getMessage() {
      switch ('create') {
        case 'create': return 'Успешно создано'
        case 'edit': return 'Успешно сохранено'	
      }
    },
  },
  async mounted() {
    await this.changeNomination(this.userInfo.nomination[0].id)
  },
  methods: {
    async changeNomination(key) {
      this.loading = true
      this.activeNomination = key
      await this.getIndicatorsByNomination(this.activeNomination)
      this.indicators = await this.getAchievementsToIndicators(this.initialIndicators)
      console.log(this.indicators)
      this.loading = false
    },
    async getIndicatorsByNomination(id) {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: INDICATOR,
          params: {
            nomination: id,
          },
        })
      } catch (e) {
        console.log(e)
      }
    },
    async getAchievementsToIndicators(array) {
      return await Promise.all(array.map(indicator => {
        return new Promise(async res => {
          const achievements = await this.$store.dispatch('app/getEntities', {
            entityName: ACHIEVEMENT,
            params: {
              indicator: indicator.id
            }
          })
          console.log(achievements)
          indicator.achievements = achievements.data.map(achievement => {
            if (achievement.document) {
              achievement.documentName = this.addDocumentName(achievement.document)
            }
            return achievement
          })
          res(indicator)
        })
      }))
    },
    getFormData(form) {
      const formData = new FormData()
      for (let i in form) {
        formData.append(i, form[i])
      }
      return formData
    },
    async saveAchievement(form) {
      const formData = this.getFormData(form)
      await this.saveEntity(formData)
    },
    async saveEntity(data) {
      try {
        this.buttonDisabled = true
        const response = await this.$store.dispatch('app/saveEntity', {
          data,
          method: 'post',
          entityName: ACHIEVEMENT,
        })
        this.addNewFields(response)
        this.$notification.success({ message: this.getMessage })
      } catch (e) {
        console.log(e)
        this.$formulate.handle(e.response.data, 'achievement')
      } finally {
        this.buttonDisabled = false
      }
    },
    addDocumentName(document) {
      if (document) {
        const split = document.split('/')
        return split[split.length-1]
      }
    },
    addNewFields(data) {
      if (data.document) {
        data.documentName = this.addDocumentName(data.document)
      }
      const currentIndicatorIndex = this.indicators.findIndex(
        item => data.indicators === item.id,
      )
      const currentIndicator = this.indicators[currentIndicatorIndex]
      currentIndicator.achievements.push(data)
      // this.indicators.splice(currentIndicatorIndex, 1, currentIndicator)
      console.log(this.indicators)
    },
    async removeAchievement(id) {
      this.loading = true
      try {
        await this.$store.dispatch('app/deleteItem', {
          entityName: ACHIEVEMENT,
          id,
        })
        await this.changeNomination()
        this.$swal('Успешно удалено!')
      } catch (e) {
        console.log(e)
        this.$errors.showOnce(e.response.data)
      } finally {
        this.loading = false
      }
    },
    editAchievement() {},
    trimWhitespaces(object) {
      for (let i in object) {
        if (object[i] === String) object[i].trim()
      }
    },
    async onApplicate() {
      const { isConfirmed } = await this.$swal.fire({
        title: 'Отправить завку на конкурс? После подтверждения действия Вы не сможете редактировать введенные данные',
        inputLabel: 'Your email address',
        showCancelButton: true,
      })
    }
  },
}
