<template>
  <li class="achievement-list__item">
    <div class="achievement-list__item--read">
      <div class="achievement-list__item--tools">
        <span class="count">{{ position }}</span>
        <a-tooltip placement="top">
          <template slot="title">
            <span>Удалить достижение</span>
          </template>
          <v-icon @click="deleteAchievement" class="remove" name="times"></v-icon>
        </a-tooltip>
      </div>
      <div class="achievement-list__item--read--item item-name">
        <!-- <span>Название достижения</span> -->
        <span>{{ item.name }}</span>
      </div>
      <div class="achievement-list__item--read--item item-participation" v-if="item.degree_participation">
        <!-- <span>Степень участия</span> -->
        <span>{{ item.degree_participation }}</span>
      </div>
      <div class="achievement-list__item--read--item item-period">
        <!-- <span>Год</span> -->
        <span>{{ item.period }}</span>
      </div>
      
    </div>
    <div class="achievement-list__item--read--item" v-if="item.url">
        <!-- <span>Ссылка на видео</span> -->
        <a :href="item.url" target="_blank">{{ item.url }}</a>
      </div>
    <div class="file-container" v-if="item.document">
      <div class="file-container__files">
        <a :href="getDocumentLink" target="_blank" class="file-container__files--file">
          <v-icon name="file"></v-icon>
          <span>{{ item.documentName }}</span>
        </a>
      </div>
    </div>
  </li>
</template>

<script>
import FileComponent from '@/components/Ui/file'
const { VUE_APP_API_MEDIA } = process.env
export default {
  components: {
    FileComponent,
  },
  data() {
    return {
      media: VUE_APP_API_MEDIA
    }
  },
  props: {
    participationDegree: {
      type: Array,
      required: true,
    },
    position: {
      type: Number,
      required: true,
    },
    setFiles: {
      type: Function,
      required: false,
    },
    item: {
      type: Object,
      required: true,
    },
  },
  computed: {
    getDocumentLink() {
      return `${this.media}/${this.item.document}`
    }
  },
  methods: {
    async deleteAchievement() {
      const { isConfirmed } = await this.$swal.fire({
        title: 'Вы точно хотите удалить достижение?',
        showCancelButton: true,
      })
      if (isConfirmed) this.$emit('remove', this.item.id)
    }
  }
}
</script>

<style lang="scss" scoped>
.count {
  display: block;
  align-self: center;
  color: #d1d1d1;
  font-weight: 500;
  font-size: 20px;
}
</style>
