<template>
  <div class="document-container">
    <h6 class="document-title">
      {{ indicator.name }}
    </h6>
    <div class="document-clue" v-if="indicator.hint_text">
      {{ indicator.hint_text }}
    </div>
    <div>
      <ol class="achievement-list">
        <upload-achievements-item-info
          v-for="(achievement, key) in indicator.achievements"
          :key="achievement.id"
          :position="key + 1"
          :participationDegree="participationDegree"
          :setFiles="setFiles"
          :item="achievement"
          :disabled="true"
          :indicator="indicator"
          @remove="achievement_id => $emit('remove', achievement_id)"
        >
        </upload-achievements-item-info>
        <x-form
          :values="form"
          v-model="form"
          name="achievement"
          @submit="form => submit(indicator.id)"
        >
          <upload-achievements-item
            :setFiles="setFiles"
            :key="achievements.length + 1"
            :position="achievements.length + 1"
            :participationDegree="participationDegree"
            :indicator="indicator"
          >
          </upload-achievements-item>
        </x-form>
      </ol>
    </div>
  </div>
</template>

<script>
import FileComponent from '@/components/Ui/file'
import UploadAchievementsItem from './UploadAchievementsItem'
import UploadAchievementsItemInfo from './UploadAchievementsItemInfo'

export default {
  components: {
    UploadAchievementsItem,
    FileComponent,
    UploadAchievementsItemInfo,
  },
  props: {
    achievements: {
      type: Array,
      required: true,
    },
    participationDegree: {
      type: Array,
      required: true,
    },
    indicator: {
      type: Object,
      required: true,
    },
    saveAchievement: {
      type: Function,
      required: true,
    },
  },
  data() {
    return {
      form: {},
    }
  },
  methods: {
    setFiles(file) {
      this.form.document = file
    },
    async submit(indicators) {
      await this.saveAchievement({ ...this.form, indicators })
      this.form = {}
    },
  },
}
</script>

