<template>
  <li class="achievement-list__item">
    <div class="achievement-list__item--info" v-if="!isVisible">
      <div class="achievement-list__item--info--block">
        <span class="count">{{ position }}</span>
        <x-input
          name="name"
          type="text"
          validation="required"
          label="Название достижения"
          class="item-name"
          error-behavior="submit"
        ></x-input>
        <x-input
          v-if="indicator.degree_participation"
          name="degree_participation"
          type="select"
          validation="required"
          :options="participationDegree"
          label="Степень участия"
          class="item-participation"
          error-behavior="submit"
        >
        </x-input>
        <x-input
          name="period"
          type="select"
          validation="required"
          :options="getPeriod"
          label="Год"
          class="item-period"
          error-behavior="submit"
        >
        </x-input>
      </div>
      <div class="achievement-list__item--info--block">
        <x-input
          style="width: 100%"
          v-if="indicator.url"
          type="url"
          name="url"
          validation="required"
          label="Вставьте ссылку на видео"
          error-behavior="submit"
        >
        </x-input>
      </div>
      <div class="achievement-list__item--info--block">
        <file-component
          v-if="indicator.document"
          @change="file => setFiles(file)"
        >
        </file-component>
      </div>
      <a-button
        type="info"
        block
        html-type="submit"
        class="document-container-button"
      >
        <v-icon name="plus"></v-icon>
        <span>Добавить</span>
      </a-button>
    </div>
  </li>
</template>

<script>
import FileComponent from '@/components/Ui/file'
export default {
  components: {
    FileComponent,
  },
  props: {
    participationDegree: {
      type: Array,
      required: true,
    },
    position: {
      type: Number,
      required: true,
    },
    setFiles: {
      type: Function,
      required: false,
    },
    indicator: {
      type: Object,
      required: true,
    },
  },
  computed: {
    isVisible() {
      return this.indicator.max_count_achievement === this.indicator.achievements.length
    },
    getPeriod() {
      const { start_achievement, finish_achievement } = this.indicator.period
      const years = []
      for (let i = start_achievement; i <= finish_achievement; i++) {
        years.push(i)
      }
      return years
    },
  },
}
</script>

<style lang="scss" scoped>
.count {
  color: #d1d1d1;
  font-weight: 500;
  font-size: 20px;
}
</style>
